/* eslint-disable react/no-unused-prop-types */
/* eslint-disable react/prop-types */
import React from "react"
import PropTypes from "prop-types"
import styled from "styled-components"
import tw from "tailwind.macro"
import { Tween, Timeline } from "react-gsap"
import Content, { Inner } from "../elements/Content"
import PlaneOne from "../images/plane1.svg"
import PlaneTwo from "../images/plane2.svg"
import PlaneThree from "../images/plane3.svg"

const ContentWrapper = styled(Content)`
  ${tw`min-h-400 lg:min-h-600 bg-grey-lightest overflow-hidden pr-8`};
  height: 130vh;
  ${props =>
    props.isDark
      ? `background: linear-gradient(to bottom right, #0b273f 0%, #07141d 100%)`
      : tw`bg-grey-lightest`};
`

const InnerWrapper = styled(Inner)`
  ${props => !props.isStandalone && tw`mb-0 md:mb-0`}
`

const PlaneOneWrapper = styled.div`
  ${tw`absolute opacity-0`};
  ${tw`w-full sm:w-3/4 lg:w-1/2`};
  right: 10%;
  top: 0;

  @media (min-width: 601px) {
    top: 0;
  }
`

const PlaneTwoWrapper = styled.div`
  ${tw`absolute opacity-0`};
  ${tw`w-full md:w-3/4 lg:w-1/2`};
  right: 0;
  bottom: -10px;

  @media (min-width: 601px) {
    bottom: -50px;
  }
`

const Human = styled.div`
  ${tw`absolute opacity-0`};
  ${tw`w-full md:w-3/4 lg:w-1/2`};
  right: -45%;
  bottom: -150px;

  @media (min-width: 901px) {
    right: -25%;
  }

  @media (min-width: 1201px) {
    right: -20%;
    bottom: -200px;
  }
`

const HeroIndex = ({ children, isDark }) => {
  const mainPaused = false

  return (
    <>
      <Timeline paused={mainPaused}>
        <Tween
          staggerFrom={{ opacity: 0 }}
          staggerTo={{ opacity: 1 }}
          stagger={0.17}
          duration={1.95}
          position="+=0"
          ease="Power3.easeOut"
        >
          <PlaneOneWrapper>
            <PlaneOne />
          </PlaneOneWrapper>
          <PlaneTwoWrapper>
            <PlaneTwo />
          </PlaneTwoWrapper>
        </Tween>
        <Tween
          from={{ opacity: 0.3, y: "400px" }}
          to={{ opacity: 0.3, y: "0" }}
          duration={1.5}
          delay={-1.75}
          ease="Power3.easeOut"
        >
          <Human>
            <PlaneThree />
          </Human>
        </Tween>
      </Timeline>
      <HeroInner isDark={isDark} isPaused={mainPaused}>
        {children}
      </HeroInner>
    </>
  )
}

const HeroInner = ({ children, isStandalone }) => (
  <InnerWrapper isStandalone={isStandalone}>
    <Timeline paused={false}>
      <Tween
        staggerFrom={{ opacity: 0, y: "200px" }}
        staggerTo={{ opacity: 1, y: "0" }}
        stagger={0.21}
        duration={0.75}
        delay={isStandalone ? 0 : -0.1}
        position={isStandalone ? "+=0" : "+=1"}
        ease="Power3.easeOut"
      >
        {children}
      </Tween>
    </Timeline>
  </InnerWrapper>
)

const Hero = ({ children, isIndex, isDark }) => (
  <>
    <Tween to={{ height: "80vh" }} ease="Power3.easeOut" duration={1.5}>
      <ContentWrapper isDark={isDark}>
        {isIndex ? (
          <HeroIndex isDark={isDark}>{children}</HeroIndex>
        ) : (
          <HeroInner isStandalone>{children}</HeroInner>
        )}
      </ContentWrapper>
    </Tween>
  </>
)

export default Hero

Hero.defaultProps = {
  IsIndex: false,
  IsDark: false,
}

Hero.propTypes = {
  children: PropTypes.node.isRequired,
  IsIndex: PropTypes.bool,
  IsDark: PropTypes.bool,
}
