import React, { useState } from "react"
import styled, { css } from "styled-components"
import tw from "tailwind.macro"
import { Link } from "gatsby"
import LinkTo from "../components/LinkTo"
import { Container, Inner } from "../elements/Content"
import FLogo from "../images/logo-full.svg"

const Nav = styled(Container)`
  ${tw`py-4 bg-grey-lightest`};
`
const NavInner = styled(Inner)`
  ${tw`flex items-center justify-between flex-wrap`}
`

const LogoWrapper = styled.a`
  ${tw`flex text-black hover:text-black mr-4`};
  height: 40px;
`

const Logo = styled(FLogo)`
  height: 100%;
`

const TriggerWrapper = styled.div`
  ${tw`block lg:hidden`}
`
const Trigger = styled.button`
  ${tw`flex items-center px-3 py-2 bg-white border rounded text-grey-dark border-grey-dark hover:text-grey-darkest hover:border-grey-darkest`}
`
const TriggerIcon = styled.svg`
  ${tw`fill-current h-3 w-3`}
`

const hiddenSmallOnly = css`
  ${tw`hidden lg:flex`};
`
const ListWrapper = styled.div`
  ${tw`w-full block flex-grow lg:flex lg:items-center lg:w-auto`};
  ${props => props.navOpen && hiddenSmallOnly};
`

const List = styled.div`
  ${tw`text-base lg:flex-grow pt-1`}
`
const ListItem = styled.span`
  ${tw`block lg:inline-block mt-4 lg:mt-0 mr-4`};
  ${tw`px-4 lg:px-0`};
  ${tw`font-sans text-grey-dark hover:text-grey-darkest`};
  ${tw`text-xl lg:text-base cursor-pointer`};
`
const ListItemCTA = styled(Link)`
  ${tw`inline-block mt-4 lg:mt-0 px-4 py-2  border rounded border-white hover:border-transparent hover:bg-white`}
  ${tw`font-sans font-bold leading-none text-grey-dark hover:text-grey-darkest`};
  ${tw`text-xl lg:text-base`};
`

const Navigation = () => {
  const [open, setOpen] = useState(true)

  return (
    <>
      <Nav>
        <NavInner>
          <LogoWrapper href="/">
            <Logo />
          </LogoWrapper>
          <TriggerWrapper>
            <Trigger onClick={() => setOpen(!open)}>
              <TriggerIcon
                viewBox="0 0 20 20"
                xmlns="http://www.w3.org/2000/svg"
              >
                <title>Menu</title>
                <path d="M0 3h20v2H0V3zm0 6h20v2H0V9zm0 6h20v2H0v-2z" />
              </TriggerIcon>
            </Trigger>
          </TriggerWrapper>
          <ListWrapper navOpen={open}>
            <List>
              <LinkTo href="/team">
                <ListItem>Meet the Team</ListItem>
              </LinkTo>
            </List>
            <ListItemCTA to="/#contact">Contact</ListItemCTA>
          </ListWrapper>
        </NavInner>
      </Nav>
    </>
  )
}

export default Navigation
export { LinkTo }
